.newsletter {
    text-align: center;
    margin-bottom: 7rem;
    padding: 4rem 0;
    background: linear-gradient(to bottom, #f887ef69, white 80%);


    .newsletter_title {
        font-size: 48px;
    }
    p {
        margin: 1rem 0;
    }

    form {
        border: 1px solid gray;
        background-color: #fff;
        max-width: 500px;
        height: 60px;
        display: flex;
        margin: 0 auto;
        border-radius: 50px;
        overflow: hidden;


        input {
            flex: 9;
            border: none;
            text-indent: 15px;
        }
        button {
            flex: 3;
            border: none;
            border-radius: inherit;
            background-color: black;
            color: #fff;
        }

    }
}