footer {

    .footer_wrapper {
        text-align: center;
        

        .logo_container {
            display: flex;
            align-items: center;
            justify-content: center;
            padding: 1rem 0;
            margin-bottom: 2rem;
            
            h2 {
                margin-top: 2rem;
            }
        }

        ul {
            display: flex;
            justify-content: center;
            align-items: center;
            margin-bottom: 2rem;

            li {
                margin: 0 1rem;
            }
        }

        .socila_icons {
            margin-bottom: 2rem;
            padding-bottom: 2rem;
            border-bottom: 2px solid gray;

            img {
                margin: 0 1rem;
            }
        }
        .copyright {
            padding-bottom: 2rem;
        }
    }
}