 .category {
    
    .category_banner {
        margin: 3rem 0;

        img {
            width: 100%;
        }
    }

    .products_wrapper{
        
        .header {
            padding: 1rem 0;
            display: flex;
            justify-content: space-between;

            select {
                min-width: 200px;
                outline: none;
                padding: 0.5rem 1rem;

            }
        }

        .article_wrapper {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 2rem;
            
        }


    }
 }