.hero {
  height: calc(100vh - 100px);
  background: linear-gradient(to bottom, #f887ef, white 50%);

  .hero_wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;

    .left_side {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: start;

      h3 {
        font-size: 21px;
      }

      h1 {
        font-size: 68px;
        position: relative;
        margin: 2rem 0;
        img {
          width: 100px;
          position: absolute;
          top: 0;
        }
      }

      .btn_collection {
        background-color: var(--first-color);
        border: none;
        padding: 1rem 1.5rem;
        border-radius: 50px;
        color: #fff;
        img {
          margin-left: 1rem;
        }
      }
    }
    .hero_image_wrapper {
        display: flex;
        justify-content: end;
        
      img {
        margin-top: 2rem;
        width: 90%;
      }
    }
  }
}
