.cart_items {
  min-height: calc(100vh - 100px);
  margin-top: 3rem;

  .cart_items_table {
    width: 100%;
    
    
    thead tr th {
        padding: 1rem;
        text-align: left;
      
    }
    tbody tr td {
        padding: 1rem;
        border-bottom: 1px solid gray;

        .product_image {
            width: 50px;
        }
        .remove_icon {
            cursor: pointer;
        }
    }
  }

  .amounts {
    margin-top: 5rem;
    display: flex;
    
    .amount_infos {
      flex: 2;
      margin-right: 10rem;

      .info_title{
        margin-bottom: 2rem;
      }
      p {
        display: flex;
        justify-content: space-between;
        padding: 1em 0;
      }
      p:not(p.total) {
        border-bottom: 1px solid gray;

      }

      .btn_proceed {
        padding: 1rem;
        margin-top: 1rem;
        background-color: var(--first-color);
        color: #fff;
        border: none;
      }
    }

    form {
      flex: 3;
      display: flex;
      height: 50px;
      width: 300px;

      input {
        max-width: 400px;
        width: 100%;
        border: none;
        outline: none;
background-color: rgb(221, 221, 221);
text-indent: 1rem;
      }
      .btn_copon {
        padding: .4rem 3rem;
        border: none;
        background-color: black;
        color: #fff;
      }
    }
  }
}
