.single_product {

    min-height: calc(100vh - 100px);
    margin-bottom: 6rem;

    .breadcramp {
        margin-top: 2rem;
        margin-bottom: 3rem;
        font-weight: bold;
        font-size: 22px;
    }

    .product_container {
        display: flex;
        justify-content: space-between;
        height: 100%;
        gap: 1.9rem;

        .images {
            flex: 6;
            display: flex;
            gap: 1rem;
            
            
            .image_gallery {
                display: flex;
                flex-direction: column;
                flex: 2;
                gap: 1rem;

                img {
                    width: 100%;
                }
            }
            .product_image {
                flex: 9;

                img {
                    width: 100%;
                }
            }
        }


        .product_info {
            flex: 5;
            display: flex;
            flex-direction: column;
            align-items: start;

            .product_title {
                font-size: 38px;
            }
            .stars {
                margin: 1rem 0 ;
            }

            .prices {
                display: flex;
                font-size: 32px;
                margin-bottom: 1rem;

                .old_price {
                    color: gray;
                    text-decoration: line-through;
                    margin-right: 2rem;
                }
                .new_price {
                    color: var(--first-color);
                }
            }

            .description {
                margin-bottom: 5rem;    
            }
            .sizes {
                margin-bottom: 1rem;
                p {
                    margin-bottom: 0.5rem;
                }

                .sizes_container {
                    .btn_size {
                        margin-right: 0.5rem;
                        aspect-ratio: 1 / 1;
                        width: 40px;
                    }
                }
            }

            .btn_addtocart {
                margin-bottom: 1rem;
                background-color: var(--first-color);
                border: none;
                padding: 1rem 2rem;
                color: #fff;
                font-weight: bold;
                
            }
            .catefories {
                margin-top: 3rem;
            }
        }

    }

    .related_products {
        margin-top: 5rem;

        .title {
            text-align: center;
            margin-bottom: 1rem;
        }

        .products_wrapper {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            gap: 2rem;
        }
    }
}