nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100px;

  .left_side a {
    display: flex;
    align-items: center;

    h3 {
        margin-top: 1rem;
    }
  }

  ul {
    display: flex;
    align-items: center;

    li {
      margin: 1rem;
      color: black;

      a {
        position: relative;
        height: 100%;
        height: 100%;

        hr {
          position: absolute;
          width: 100%;
          top: -50%;
          border: 1px solid var(--first-color);
        }
      }
    }
  }

  .right_side {
    display: flex;
    align-items: center;

    .btn {
      margin-right: 2rem;
      border-radius: 50px;
      color: var(--first-color);
    }
    .busket_image_wrapper {
      position: relative;
      
      img {
        aspect-ratio: 1 / 1;
        width: 30px;
        
      }
      .notify {
        background-color: var(--first-color);
        color: #fff;
        padding: 5px;
        aspect-ratio: 1 / 1;
        min-width: 20px;
        min-height: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 20px;
        position: absolute;
        top: -50%;
        right: -50%;
      }
    }
  }
}
