  @import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;
    font-family: 'Poppins', sans-serif;
    transition: all 0.3s ease;
}

main {
    max-width: 1500px;
    margin: 0 auto;
    padding: 0 50px;
    width: inherit;
    height: inherit;

}

ul {
    list-style: none;
}

a {
    font-size: inherit;
    color: inherit;
}

:root {
    --first-color: #ff4141;

}
.btn {
    padding: .3rem 1rem;
    border: 1px solid grey;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
}
button {
    cursor: pointer;
}
input {
    outline: none;
}