.popluar_products {
  margin-bottom: 7rem;

  .popular_title {
    font-size: 34px;
    margin: 2rem 0;
    margin-bottom: 3rem;
    text-align: center;
  }

  .popular_products_wrapper {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 2rem;
  }
}

// this for item card
.item {
  margin-bottom: 1rem;

  img {
    width: 100%;
    transition: all 0.3s ease;
  }
  &:hover img {
    scale: 1.02;
  }

  .contents {

    padding: 0 0.5rem;

    .product_title {
      margin: 1rem 0;
    }
    .prices {
      display: flex;

      .old_price {
        margin-left: 1rem;
        text-decoration: line-through;
        color: gray;
      }
    }    
  }
}
